import React, { useState, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import axios from 'axios';

export default function NonMemberConfirmation() {
  const location = useLocation();
  const navigate = useNavigate();
  const { id, nom, prenom, dateNaissance, tarif, methodePaiement } = location.state || {};
  
  const [status, setStatus] = useState('pending');
  
  useEffect(() => {
    if (!id) return;
    
    // Fonction pour vrifier le statut du paiement
    const checkPaymentStatus = async () => {
      try {
        const response = await axios.get(`http://localhost:4000/presences/${id}`);
        if (response.data.success) {
          const presenceStatus = response.data.presence.status;
          if (presenceStatus === 'Pay' || presenceStatus === 'Annul') {
            setStatus(presenceStatus);
            // Rediriger vers la page d'accueil aprs 2 secondes
            setTimeout(() => {
              navigate('/');
            }, 2000);
          }
        }
      } catch (error) {
        console.error('Erreur lors de la vrification du statut:', error);
      }
    };
    
    // Vrifier le statut toutes les 2 secondes
    const intervalId = setInterval(checkPaymentStatus, 2000);
    
    // Nettoyer l'intervalle lors du dmontage du composant
    return () => clearInterval(intervalId);
  }, [id, navigate]);
  
  // Rediriger si les donnes sont manquantes
  if (!id || !nom || !prenom) {
    navigate('/non-member');
    return null;
  }
  
  const formatDate = (dateString) => {
    const options = { day: '2-digit', month: '2-digit', year: 'numeric' };
    return new Date(dateString).toLocaleDateString('fr-FR', options);
  };
  
  return (
    <div className="confirmation-container">
      <div className="confirmation-card">
        {status === 'Pay' ? (
          <>
            <h2 className="success-title">Paiement valid !</h2>
            <p>Votre paiement a t valid avec succs.</p>
            <p>Vous allez tre redirig vers la page d'accueil...</p>
          </>
        ) : status === 'Annul' ? (
          <>
            <h2 className="canceled-title">Paiement annul</h2>
            <p>Votre paiement a t annul.</p>
            <p>Vous allez tre redirig vers la page d'accueil...</p>
          </>
        ) : (
          <>
            <h2>En attente de validation</h2>
            <p>Votre demande a t enregistre et est en attente de validation par un bnvole.</p>
            <p className="highlight-text">Veuillez vous prsenter  l'accueil pour finaliser votre paiement.</p>
          </>
        )}
        
        <div className="info-box">
          <h3>Rcapitulatif</h3>
          <div className="info-row"><span>Nom :</span><span>{nom}</span></div>
          <div className="info-row"><span>Prnom :</span><span>{prenom}</span></div>
          <div className="info-row"><span>Date de naissance :</span><span>{formatDate(dateNaissance)}</span></div>
          <div className="info-row"><span>Tarif :</span><span>{tarif === 0 ? 'Gratuit' : `${tarif}`}</span></div>
          <div className="info-row"><span>Mthode de paiement :</span><span>{methodePaiement}</span></div>
        </div>
      </div>
    </div>
  );
}
