import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { playBuzzerSound, playBellSound, playSuccessSound, preloadSounds } from '../utils/soundUtils';

export default function MemberCheck() {
  const [nom, setNom] = useState('');
  const [prenom, setPrenom] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [message, setMessage] = useState('');
  const [benevoleCalled, setBenevoleCalled] = useState(false);
  const navigate = useNavigate();

  // Pr-charger les sons au chargement du composant
  useEffect(() => {
    preloadSounds();
  }, []);

  const handleVerification = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError('');
    setMessage('');
    setBenevoleCalled(false);

    try {
      // tape 1: Vrification de l'adhsion
      const checkResponse = await axios.get('http://localhost:4000/members/check', {
        params: { nom, prenom }
      });

      if (checkResponse.data.success) {
        // Membre valide et pay
        setMessage(checkResponse.data.message);
        
        // Jouer le son de succs (optionnel)
        playSuccessSound();
        
        // tape 2: Enregistrement de la prsence SANS tarif pour les adhrents valides
        const presenceResponse = await axios.post('http://localhost:4000/presences', {
          type: 'adherent',
          nom,
          prenom
          // Pas de tarif spcifi - le membre valide n'a pas besoin de payer
        });

        if (presenceResponse.data.success) {
          // Redirection aprs succs
          setTimeout(() => {
            navigate('/confirmation');
          }, 2000);
        } else {
          setError("Erreur lors de l'enregistrement de la prsence");
          playBuzzerSound(); // Jouer le son d'erreur
        }
      } else {
        // Membre non valide ou non pay - JOUER LE BUZZER
        const errorMessage = checkResponse.data.error || "Adhsion non valide";
        setError(errorMessage);
        
        // Jouer le son buzzer pour les erreurs
        playBuzzerSound();
      }

    } catch (error) {
      console.error('Erreur:', error);
      let errorMessage = "Erreur de connexion";
      
      if (error.response?.status === 404) {
        errorMessage = "Aucun membre trouv avec ce nom et prnom";
      } else if (error.response?.data?.error) {
        errorMessage = error.response.data.error;
      } else if (error.message) {
        errorMessage = error.message;
      }
      
      setError(errorMessage);
      
      // Jouer le son buzzer pour toutes les erreurs
      playBuzzerSound();
    } finally {
      setLoading(false);
    }
  };

  const handleAppelerBenevole = () => {
    // Jouer le son de cloche
    playBellSound();
    
    // Marquer que le bnvole a t appel
    setBenevoleCalled(true);
    
    // Afficher un message de confirmation
    setMessage("Un bnvole va arriver pour vous aider !");
    
    // Effacer le message aprs 5 secondes
    setTimeout(() => {
      if (benevoleCalled) {
        setMessage('');
        setBenevoleCalled(false);
      }
    }, 5000);
  };

  const handleRetourAccueil = () => {
    // Effacer les champs et messages avant de revenir  l'accueil
    setNom('');
    setPrenom('');
    setError('');
    setMessage('');
    setLoading(false);
    setBenevoleCalled(false);
    
    // Naviguer vers la page d'accueil
    navigate('/');
  };

  return (
    <div className="member-check">
      <div className="header-section">
        <h2>Vrification d'adhsion</h2>
        <div className="header-buttons">
          <button 
            type="button" 
            className="btn-appeler-benevole"
            onClick={handleAppelerBenevole}
            disabled={loading}
          >
             Appeler un bnvole
          </button>
          <button 
            type="button" 
            className="btn-retour-accueil"
            onClick={handleRetourAccueil}
            disabled={loading}
          >
             Retour  l'accueil
          </button>
        </div>
      </div>
      
      <form onSubmit={handleVerification}>
        <div className="form-group">
          <label>Nom</label>
          <input
            type="text"
            value={nom}
            onChange={(e) => setNom(e.target.value)}
            required
            disabled={loading}
          />
        </div>
        
        <div className="form-group">
          <label>Prnom</label>
          <input
            type="text"
            value={prenom}
            onChange={(e) => setPrenom(e.target.value)}
            required
            disabled={loading}
          />
        </div>
        
        <button type="submit" disabled={loading} className="btn-verify">
          {loading ? 'Vrification...' : 'Vrifier'}
        </button>
      </form>
      
      {error && (
        <div className="error-message">
          <div className="error-icon"></div>
          {error}
        </div>
      )}
      
      {message && (
        <div className={benevoleCalled ? "benevole-message" : "success-message"}>
          <div className="success-icon">
            {benevoleCalled ? "" : ""}
          </div>
          {message}
        </div>
      )}
    </div>
  );
}
