/**
 * Utilitaire pour jouer des sons dans l'application
 */

// Fonction pour jouer un son
export const playSound = (soundPath) => {
  try {
    // Crer un nouvel objet Audio
    const audio = new Audio(soundPath);
    
    // Dfinir le volume (0.0  1.0)
    audio.volume = 0.8;
    
    // Jouer le son
    audio.play()
      .then(() => {
        console.log('Son jou avec succs:', soundPath);
      })
      .catch(error => {
        console.error('Erreur lors de la lecture du son:', error);
      });
  } catch (error) {
    console.error('Erreur lors de la cration de l\'objet Audio:', error);
  }
};

// Fonction spcifique pour le son buzzer
export const playBuzzerSound = () => {
  // Chemin vers le fichier MP3 buzzer dans le dossier public/assets
  const buzzerPath = '/assets/buzzer.mp3';
  playSound(buzzerPath);
};

// Fonction spcifique pour le son de cloche (bell)
export const playBellSound = () => {
  // Chemin vers le fichier MP3 bell dans le dossier public/assets
  const bellPath = '/assets/bell.mp3';
  playSound(bellPath);
};

// Fonction pour jouer un son de succs (optionnel)
export const playSuccessSound = () => {
  // Vous pouvez ajouter un son de succs si disponible
  const successPath = '/assets/success.mp3';
  playSound(successPath);
};

// Fonction pour pr-charger les sons (optionnel mais recommand)
export const preloadSounds = () => {
  try {
    // Pr-charger le son buzzer
    const buzzerAudio = new Audio('/assets/buzzer.mp3');
    buzzerAudio.preload = 'auto';
    
    // Pr-charger le son bell
    const bellAudio = new Audio('/assets/bell.mp3');
    bellAudio.preload = 'auto';
    
    console.log('Sons pr-chargs');
  } catch (error) {
    console.error('Erreur lors du pr-chargement des sons:', error);
  }
};
